<template>
  <div class="wedding-invitation" :class="{ 'invitation-bounce':canOpen }" >
    <div class="invitation-container" :class="{ 'invitation-down':isOpening }">
      <div class="invitation-cover">
        <div @click="onExit" class="cover-content" :class="{'invitation-up':isOpening}" >
          <Cake v-if="isOpening" class="go-top"></Cake>
        </div>
        <div class="cover-inside-left" :class="{'opening':isOpening}"></div>
        <div class="cover-inside-right" :class="{'opening':isOpening}"></div>
        <img class="cover-inside-seal" src="../images/seal.png" @click="openInvitation" :class="{'invitation-flight':isOpening}">
      </div>
    </div>
  </div>
</template>

<script>
import Cake from "./Cake"

export default {
  props: ['canOpen'],
  components: { Cake },
  data() {
    return {
      isOpening: false,
      cakeLoaded: false
    }
  },
  methods: {
    // 打开邀请函
    openInvitation(){
      this.isOpening = true
      setTimeout(
        () => {this.sendBarrage(); this.cakeLoaded=true}
        , 6000
      )
    },
    // 发送弹幕
    sendBarrage(){
      this.$nextTick(() => {
        setTimeout(() => {
          this.$emit('sendBarrage')
        }, 660)
      })
    },
    onExit(){
      if (this.cakeLoaded){
        this.isOpening = false
        setTimeout(() => {
          this.$emit('exit')
        }, 666)
      }
    }
  }
}
</script>

